import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import firefliesVertexShader from './shaders/fireflies/vertex.glsl'
import firefliesFragmentShader from './shaders/fireflies/fragment.glsl'
import portalVertexShader from './shaders/portal/vertex.glsl'
import portalFragmentShader from './shaders/portal/fragment.glsl'
import * as ZapparThree from "@zappar/zappar-threejs";
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';
/**
 * Base
 */


// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Loaders
 */
// Texture loader
const textureLoader = new THREE.TextureLoader()
const manager1 = new ZapparThree.LoadingManager();
let globalLoader = new ZapparThree.DefaultLoaderUI();


// GLTF loader
const gltfLoader = new GLTFLoader(manager1)


const bakedTexture = textureLoader.load('./baked.jpg')
bakedTexture.flipY = false
bakedTexture.encoding = THREE.sRGBEncoding
const portalLightMaterial = new THREE.ShaderMaterial({
    uniforms:{
        uTime:{value:0}
    },
    vertexShader: portalVertexShader,
    fragmentShader: portalFragmentShader,
    side: THREE.DoubleSide
})
const bakedMaterial = new THREE.MeshBasicMaterial({map: bakedTexture})
const poleLightMaterial = new THREE.MeshBasicMaterial({color: '#EEDD82'})

gltfLoader.load(
    './portal.glb',(gltf)=>
    {
       const bakedMesh = gltf.scene.children.find((child)=> child.name === 'baked')
       const portalLight = gltf.scene.children.find((child) =>  child.name === 'portalLight')
       const poleLightA = gltf.scene.children.find((child) => child.name === 'poleLightA')
       const poleLightB = gltf.scene.children.find((child) => child.name === 'poleLightB')
        bakedMesh.material = bakedMaterial
        portalLight.material = portalLightMaterial
        poleLightA.material = poleLightMaterial
        poleLightB.material = poleLightMaterial
      
        scene.add(gltf.scene)
    }
)
const loader = new FontLoader();

loader.load( 'fonts/helvetiker_regular.typeface.json', ( font )=> {

	const textGeometry = new TextGeometry( 'THE DOORWAY', {
		font: font,
		size: 0.12,
		height: 0.1,
		curveSegments: 2,
		bevelEnabled: true, 
		bevelThickness: 0.001,
		bevelSize: 0.01,
		bevelOffset: 0,
		bevelSegments: 4
	})
    textGeometry.computeBoundingBox()
    textGeometry.translate(
        - (textGeometry.boundingBox.max.x)*0.5, 
        + (textGeometry.boundingBox.max.y)*1.5,
        - (textGeometry.boundingBox.max.z)*14
    )
    const material = new THREE.MeshBasicMaterial({color: '#4B3621'})
    const text = new THREE.Mesh(textGeometry, material)
    scene.add(text)
});

const firefliesGeometry = new THREE.BufferGeometry()
const firefliesCount = 60
const positionArray = new Float32Array(firefliesCount*3)
const scaleArray = new Float32Array(firefliesCount)
for(let i = 0; i < firefliesCount; i++)
{
    positionArray[i*3] = (Math.random()-0.5) * 3  
    positionArray[i*3+1] = (Math.random() * 2)
    positionArray[i*3+2] = (Math.random()-0.5) * 3  
    scaleArray[i*3] = (Math.random())
}
firefliesGeometry.setAttribute('position', new THREE.BufferAttribute(positionArray, 3))
firefliesGeometry.setAttribute('aScale', new THREE.BufferAttribute(scaleArray,1))

const firefliesMaterial = new THREE.ShaderMaterial({
  
    uniforms:{
        uPixelRatio: {value: Math.min(window.devicePixelRatio,2)},
        uSize: {value: 100},
        uTime:{value:0}
    },
    vertexShader: firefliesVertexShader,
    fragmentShader: firefliesFragmentShader,
    depthWrite: false,
    blending: THREE.AdditiveBlending,
    transparent: true
})

const fireflies = new THREE.Points(firefliesGeometry,firefliesMaterial)
scene.add(fireflies)
/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()
    firefliesMaterial.uniforms.uPixelRatio.value = Math.min(window.devicePixelRatio,2)
    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(95, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0.5
camera.position.z = 2.3
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.minPolarAngle = Math.PI / 2.5; 
controls.maxPolarAngle = Math.PI / 2.5;
controls.minAzimuthAngle = -Infinity; // radians
controls.maxAzimuthAngle = Infinity; // radians
controls.enableZoom = true;
controls.enablePan = true;





/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.outputEncoding = THREE.sRGBEncoding
renderer.setClearColor('#654321')
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    firefliesMaterial.uniforms.uTime.value = elapsedTime
    portalLightMaterial.uniforms.uTime.value = elapsedTime

    // Update controls
    controls.update()
   

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()